
import XCircle from 'vux/src/components/x-circle/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'

import axios from 'axios'
import * as util from '../api/common'

export default {
components: {
XCircle, Cell, Group
},
data () {
return {
old_password: '',
new_password: '',
new_password2: ''
}
},
mounted () {

},
methods: {
getData () {
let params = new URLSearchParams()
var self = this
if (self.old_password === '') {
self.$vux.toast.text('请输入原始密码')
return
} else if (self.new_password === '') {
self.$vux.toast.text('请输入新密码')
return
} else if (self.new_password2 === '') {
self.$vux.toast.text('请确认新密码')
return
} else if (self.new_password !== self.new_password2) {
self.$vux.toast.text('两次密码不一致')
return
}
var url = ''
if (localStorage.getItem('userType') === 'shopeeker') {
url = 'shopeeker/center/passwordReset'
} else {
url = 'buyer/center/passwordReset'
}
params.append('old_password', self.old_password)
params.append('new_password', self.new_password)
var instance = axios.create({ baseURL: util.baseURL })

instance.defaults.headers.common['Authorization'] = 'Bearer ' + localStorage.getItem('token')
instance.post(url, params).then(res => {
if (res.data.code === 200) {
self.$vux.toast.text(res.data.message)
console.log(res.data)
setTimeout(function () {
self.$router.push('/login?userType=' + localStorage.getItem('userType'))
}, 1500)
} else {
console.log(res.data)
self.$vux.toast.text(res.data.message)
}
})
}
}
}



